<template>
  <div>
  <v-form v-model="validTNHS" ref="formTiepNhanHoSo" lazy-validation>
    <div class="row-header">
      <div class="background-triangle-big"> 
        <span>IMPORT H S</span>
      </div>
      <div class="layout row wrap header_tools row-blue">
        <div class="flex xs8 sm10 pl-3 text-ellipsis text-bold" :title="thongTinChiTietHoSo.serviceName">
          {{thongTinChiTietHoSo.serviceName}}
        </div>
        <div class="flex xs4 sm2 text-right" style="margin-left: auto;">
          <v-btn flat class="my-0 mx-0 btn-border-left" @click="goBack" active-class="temp_active">
            <v-icon size="18">reply</v-icon> &nbsp;
            Quay li
          </v-btn>
        </div>
      </div> 
    </div>
    <!--  -->
    <div style="position: relative;">
      <div>
        <input type="file" id="dossierImport" @change="uploadfileDossierImport($event)" style="display:none">
        <span class="text-bold" style="color: #004b94!important">Ti ln file thng tin (nh dng: .xlsx, .xls)</span>
        <v-progress-circular
        :width="2"
        :size="25"
        color="green"
        indeterminate
        v-if="progressUploadFile"
        ></v-progress-circular>
        <v-tooltip top v-else class="ml-2">
          <v-btn slot="activator" icon class="mx-0 my-0" @click="pickFile()">
            <v-badge>
              <v-icon size="24" color="#004b94">cloud_upload</v-icon>
            </v-badge>
          </v-btn>
          <span>Ti file ln</span>
        </v-tooltip>
      </div>
    </div>
    <div style="position: relative;">
      <v-expansion-panel :value="[true]" expand  class="expansion-pl">
        <v-expansion-panel-content>
          <div slot="header" style="display: flex; align-items: center;">
            <div class="background-triangle-small"> <v-icon size="18" color="white">star_rate</v-icon></div>
            Thnh phn h s &nbsp;&nbsp;&nbsp;&nbsp;
            <span v-if="!stateEditSample && originality !== 1">({{thongTinChiTietHoSo.sampleCount === 0 ? '?' : thongTinChiTietHoSo.sampleCount}}&nbsp;b h s)</span>
            <v-text-field
            class="px-0 py-0"
            style="width: 90px; max-width: 90px;"
            v-else-if="originality !== 1"
            v-model="thongTinChiTietHoSo.sampleCount"
            type="number"
            ></v-text-field> &nbsp;
            <v-icon v-if="!stateEditSample && originality !== 1" v-on:click.stop="stateEditSample = !stateEditSample" style="cursor: pointer;" size="16" color="primary">edit</v-icon>
            <v-icon v-else-if="originality !== 1" style="cursor: pointer;" v-on:click.stop="stateEditSample = !stateEditSample" size="16" color="primary">done</v-icon>
          </div>
          <thanh-phan-ho-so ref="thanhphanhoso" :onlyView="false" :id="'nm'" :partTypes="inputTypes"></thanh-phan-ho-so>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </div>
    <!--  -->
    <!--  -->
    <!--  -->
    <v-tabs icons-and-text centered class="mb-4">
      <v-tabs-slider color="primary"></v-tabs-slider>
      <!-- <v-tab href="#tab-1" @click="luuHoSo">
        <v-btn flat class="px-0 py-0 mx-0 my-0">
          Lu &nbsp;
          <v-icon>save</v-icon>
        </v-btn>
      </v-tab> -->
      <v-tab href="#tab-5" @click="goBack" class="px-0 py-0">
        <v-btn flat class=""
          :loading="loadingAction"
          :disabled="loadingAction"
        >
          <v-icon size="18">reply</v-icon> &nbsp;
          Quay li
          <span slot="loader">Loading...</span>
        </v-btn>
      </v-tab>
    </v-tabs>
  </v-form>
  <v-dialog v-model="dialogImportDosier" max-width="800" transition="fade-transition">
      <v-card>
        <v-toolbar flat dark color="primary">
          <v-toolbar-title>Import h s</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click.native="dialogImportDosier = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text class="py-0 px-0 text-xs-center">
          <v-progress-circular
            :rotate="-90"
            :size="30"
            :width="10"
            :value="countDossierProgress"
            color="primary"
            >
            {{ countDossierProgress }}
          </v-progress-circular>
          <v-sheet
            class="d-flex"
            color="grey lighten-3"
            height="300"
            >
            <div class="pb-1" style="color: green;">
              Thnh cng: {{dossierSuccess.length}} h s
            </div>
            <div style="color: red;">
              Li: {{dossierError.length}} h s
            </div>
          </v-sheet>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click.native="addDossierAgain()"
              :loading="loadingImportDossier"
              :disabled="loadingImportDossier"
              v-if="dossierError.length"
              >
              <v-icon>save</v-icon>&nbsp;
              Thc hin li
              <span slot="loader">Loading...</span>
          </v-btn>
          <v-btn color="red" dark @click.native="dialogImportDosier = false"
            :loading="loadingImportDossier"
            :disabled="loadingImportDossier"
            >
            <v-icon>reply</v-icon>&nbsp;
            Quay li
            <span slot="loader">Loading...</span>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>

import toastr from 'toastr'
import $ from 'jquery'
import ThanhPhanHoSo from './TiepNhan/TiepNhanHoSo_ThanhPhanHoSo.vue'
toastr.options = {
  'closeButton': true,
  'timeOut': '5000'
}
export default {
  props: ['index', 'id', 'formCode'],
  components: {
    'thanh-phan-ho-so': ThanhPhanHoSo
  },
  data: () => ({
    validTNHS: false,
    dossierId: '',
    mark: true,
    stateEditSample: false,
    tiepNhanState: true,
    thongTinChiTietHoSo: {},
    payments: {},
    paymentProfile: {},
    briefNote: '',
    receiveDateEdit: '',
    editableDate: false,
    dueDateEdit: '',
    viaPortalDetail: 0,
    showThuPhi: false,
    inputTypes: [1, 3],
    outputTypes: [2],
    sampleCount: 0,
    isMobile: false,
    loadingAction: false,
    countDossierProgress: 0,
    dialogImportDosier: false,
    listDossierImport: [],
    progressUploadFile: false,
    loadingImportDossier: false
  }),
  computed: {
    loading () {
      return this.$store.getters.loading
    },
    originality () {
      var vm = this
      return vm.getOriginality()
    },
    dossierError () {
      let vm = this
      return vm.listDossierImport.filter(function (item) {
        return item.hasOwnProperty('error') && item['error']
      })
    },
    dossierSuccess () {
      let vm = this
      return vm.listDossierImport.filter(function (item) {
        return !item.hasOwnProperty('error') || !item['error']
      })
    }
  },
  created () {
    var vm = this
    vm.$nextTick(function () {
      vm.listDossierImport = []
      vm.thongTinChiTietHoSo = {}
      vm.dossierId = vm.id
      vm.initData(vm.id)
    })
  },
  beforeDestroy () {
    let viewport = $('meta[name="viewport"]')
    viewport.attr('content', 'initial-scale=1.0, width=device-width')
    if (typeof window !== 'undefined') {
      window.removeEventListener('resize', this.onResize, { passive: true })
    }
  },
  mounted () {
    this.onResize()
    window.addEventListener('resize', this.onResize, { passive: true })
  },
  watch: {
    isMobile (val) {
      let viewport = $('meta[name="viewport"]')
      if (val) {
        viewport.attr('content', '')
      } else {
        viewport.attr('content', 'initial-scale=1.0, width=device-width')
      }
    }
  },
  methods: {
    uploadfileDossierImport: function (e) {
      var vm = this
      vm.progressUploadFile = true
      var data = {}
      data['dossierId'] = vm.thongTinChiTietHoSo['dossierId']
      data['referenceUid'] = vm.thongTinChiTietHoSo['referenceUid']
      data['selector'] = 'dossierImport'
      data['partTip'] = {
        tip: '',
        maxSize: 10,
        extensions: 'xlsx,xls'
      }
      let file = $('#dossierImport')[0].files[0]
      vm.$store.dispatch('uploadDossierFileImport', data).then(function (result) {
        vm.progressUploadFile = false
        if (result.total) {
          let cf = confirm('Bn c chc chn thc hin hnh ng xa h s?')
          if (cf) {
            vm.dialogImportDosier = true
            vm.listDossierImport = result.data
            setTimeout(function () {
              if (vm.listDossierImport.length) {
                vm.loadingImportDossier = true
                let action = []
                vm.listDossierImport.forEach(function (item, index) {
                  action.push(vm.doCreateDossier(item, index))
                  Promise.all(action).then(function (result) {
                    vm.loadingImportDossier = false
                  }).catch(function (xhr) {
                    vm.loadingImportDossier = false
                  })
                })
              }
            }, 300);
          }
        } else {
          vm.listDossierImport = []
        }
      }).catch(function (xhr) {
        vm.dialogImportDosier = false
        vm.progressUploadFile = false
      })
    },
    addDossierAgain () {
      let vm = this
      vm.countDossierProgress = vm.dossierSuccess.length
      if (vm.dossierError.length) {
        vm.loadingImportDossier = true
        let action = []
        vm.dossierError.forEach(function (item, index) {
          action.push(vm.doCreateDossier(item, index))
          Promise.all(action).then(function (result) {
            vm.loadingImportDossier = false
          }).catch(function (xhr) {
            vm.loadingImportDossier = false
          })
        })
      }
    },
    doCreateDossier (item, index) {
      return new Promise((resolve, reject) => {
        let vm = this
        let thanhphanhoso = this.$refs.thanhphanhoso.dossierTemplateItems
        // if (index === 0) {
        //   changeDossierId
        // }
        let tempData = item
        tempData['serviceCode'] = vm.thongTinChiTietHoSo['serviceCode']
        tempData['govAgencyCode'] = vm.thongTinChiTietHoSo['govAgencyCode']
        tempData['dossierTemplateNo'] = vm.thongTinChiTietHoSo['dossierTemplateNo']
        tempData['originality'] = vm.originality
        console.log('data put dossier -->', tempData)
        vm.$store.dispatch('putDossier', data).then(function (result) {
          vm.countDossierProgress ++
          vm.listDossierImport[index]['error'] = false
          resolve(true)
        }).catch(reject => {
          console.log(reject)
          vm.listDossierImport[index]['error'] = true
          reject(false)
        })
      })
    },
    onResize () {
      let vm = this
      let isMobile = window.innerWidth < 1024
      vm.isMobile = isMobile
    },
    initData: function (data) {
      var vm = this
      vm.$store.dispatch('getDetailDossier', data).then(result => {
        vm.dossierId = result.dossierId
        vm.briefNote = result.dossierName ? result.dossierName : ''
        result['editable'] = false
        vm.thongTinChiTietHoSo = result
        vm.$refs.thanhphanhoso.initData(result)
      }).catch(reject => {
        console.log(reject)
      })
    },
    pickFile () {
      var vm = this
      document.getElementById('dossierImport').value = ''
      document.getElementById('dossierImport').click()
    },
    goBack () {
      let vm = this
      let currentParams = vm.$router.history.current.params
      let currentQuery = vm.$router.history.current.query
      vm.$router.push({
        path: '/danh-sach-ho-so/' + currentParams.index,
        query: currentQuery
      })
    }
  }
}
</script>
